.pageContent{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;  
    overflow: hidden;
}

.pageContent_inner{
    display: flex;
    position: relative;
    left: 5%;
    width: 90%;
    height: 99%;
    
    justify-content: center;
    align-content: center;
}

.cText {
    color:#31CB00;
    font-weight: bold;
    font-size: large;
    font-family: "Lucida Console";
}